.series,
.player{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
}

.player{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000;
  background: $black;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    width: calc(100% + 23.75em);
    height: 16.875em;
    background-image: url(../img/bg.svg);
    @include bgImgSize(100%);
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
  }
  &__video{
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__wrap{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.875em 4.0625em;
  }
  .episode-progress{
    height: .625em;
    width: 100%;
    &__wrap{
      position: relative;
      min-height: 2.1875em;
      display: flex;
      align-items: center;
    }
    &__slider{
      position: absolute;
      top: 50%;
      transform: translate(-50%,-50%);
      width: 2.1875em;
      height: 2.1875em;
      border-radius: 50%;
      background-color: #fff;
      z-index: 10;
      transition: all .3s;
      &:hover{
        background-color: $accent;
      }
    }
  }
}

.player-panel{
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 6em;
  margin-top: 1.875em;
  position: relative;
  &__time{
    font-weight: 600;
    font-size: 1.75em;
    line-height: 135%;
    letter-spacing: 0.03em;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    @include center;
    margin-left: 2.25em;
    img{
      height: 2.125em;
      width: auto;
      display: block;
    }
  }
}

.player-control{
  display: flex;
  align-items: center;
  @include centerAbsolute;
  &__play{
    margin: 0 2.5em;
    padding: 0;
    width: 6em;
    height: 6em;
    border-radius: 50%;
    &._active{
      &:after{
        background-image: url(../img/pause.svg);
        background-size: 2.25em auto;
      }
    }
    &:after{
      content: '';
      display: block;
      width: 3.125em;
      height: 3.125em;
      background-image: url(../img/play2.svg);
      @include bgImgSize(3.125em);
    }
  }
  &__prev,
  &__next{
    display: block;
    width: 2.6875em;
    height: 2.6875em;
    flex-shrink: 0;
    @include bgImgSize(100%);
  }
  &__prev{
    background-image: url(../img/prev2.svg);
  }
  &__next{
    background-image: url(../img/next2.svg);
  }
}

.player-info{
  margin-bottom: 4.375em;
  padding-left: 1.875em;
  border-left: .3125em solid $accent;
  &__quality{
    font-size: 1.75em;
    font-weight: 600;
    line-height: 136%;
  }
  &__title{
    margin: .125em 0;
  }
}

.player-settings{
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 30em;
  z-index: 10;
  padding: 5.375em 2.125em;
  overflow-y: auto;
  background: rgba(23, 21, 31, 0.75);
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  transform: translateX(100%);
  &._active{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
  &__top{
    padding: 0 2.25em;
  }
  &__items{
    margin-top: 1.75em;
  }
  &__group{
    @include lastMb(1.875em);
  }
  &__item{
    height: 5em;
    position: relative;
    padding: 0 2.1875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    border-radius: .625em;
    @include lastMb(.75em);
    &:hover{
      background-color: $accent;
    }
    &-check{
      flex-shrink: 0;
      input{
        position: absolute;
        inset: 0;
        z-index: 2;
        cursor: pointer;
        opacity: 0;
        &:checked~label{
          opacity: 1;
        }
      }
      label{
        width: 2.4375em;
        display: block;
        transition: all .3s;
        opacity: 0;
        img{
          width: 100%;
          display: block;
        }
      }
    }
  }
}

.series{
  position: fixed;
  width: 100%;
  height: $fullHeight;
  z-index: 1000;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(5px);
  overflow-y: auto;
  overflow-x: hidden;
  padding: 7em 4.375em 3.5em;
  &__title{
    text-align: center;
  }
  &__close{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    width: 2.375em;
    display: none;
    img{
      width: 100%;
      display: block;
    }
  }
  &__groups{
    margin-top: 2.75em;
  }
  &__tabs{
    display: none;
  }
}

.series-group{
  @include lastMb(2.625em);
  &__title{
    display: inline-block;
    padding: 0 .125em .625em;
    position: relative;
    margin-bottom: 1.875em;
    &:before{
      content: '';
      position: absolute;
      height: .3125em;
      width: 100%;
      left: 0;
      bottom: 0;
      background-color: $accent;
      border-radius: .5em;
    }
  }
}

.series-slider{
  .swiper{
    &-slide{
      width: 24.0625em;
      padding-right: 3.125em;
    }
  }
}

.series-card{
  &._active{
    .series-card__img:after{
      opacity: 1;
    }
  }
  &__img{
    margin-bottom: .75em;
    border-radius: .3125em;
    @include imgCover(59.7%);
    img{
      border-radius: inherit;
    }
    &:after{
      content: '';
      position: absolute;
      inset: 0;
      border-radius: inherit;
      border: .25em solid #fff;
      opacity: 0;
      transition: all .3s;
    }
  }
  &__duration{
    margin-top: .17em;
  }
}


@media screen and (max-width: 992px){
  .player{
    font-size: .7745em;
    &__wrap{
      padding: 1.25em;
    }
    &__video{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        inset: 0;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35));
      }
    }
    .episode-progress{
      height: .4375em;
      &__wrap{
        min-height: 1.25em;
      }
      &__slider{
        width: 1.25em;
        height: 1.25em;
      }
    }
  }

  .player-panel{
    min-height: unset;
    &__time{
      font-size: 1.125em;
    }
    &__btn{
      img{
        height: 1.6875em;
      }
    }
  }

  .player-control{
    position: fixed;
    &__play{
      width: 3.8125em;
      height: 3.8125em;
      margin: 0 5em;
      &:after{
        background-size: 2.0625em;
      }
      &._active{
        &:after{
          background-size: 1.4375em;
        }
      }
    }
    &__prev,
    &__next{
      width: 1.9375em;
      height: 2.25em;
    }
    &__prev{
      background-image: url(../img/prev3.svg);
    }
    &__next{
      background-image: url(../img/next3.svg);
    }
  }

  .player-info{
    margin-bottom: 1.25em;
    padding-left: 1em;
    border-width: .25em;
    &__quality{
      font-size: 1.125em;
    }
    &__title{
      margin: .1875em 0;
    }
    &__num{
      font-size: .875em;
    }
  }

  .series{
    padding: 1.25em;
    &__close{
      display: flex;
      background-color: transparent;
    }
    &__tabs,
    &__tab{
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: .1875em;
        border-radius: .5em;
      }
    }
    &__tabs{
      margin-top: 3.375em;
      display: flex;
      position: relative;
      &:before{
        background: rgba(255, 255, 255, 0.35);
      }
    }
    &__tab{
      padding: 0 1.25em .875em;
      font-weight: 600;
      position: relative;
      span{
        font-size: 1.125em;
        line-height: 139%;
      }
      &:before{
        background: $accent;
        opacity: 0;
        transition: all .3s;
      }
      &._active{
        &:before{
          opacity: 1;
        }
      }
    }
  }

  .series-group{
    display: none;
    &._active{
      display: block;
    }
    &__title{
      display: none;
    }
    .swiper{
      &-slide{
        width: 11.25em;
        padding-right: .625em;
      }
    }
  }

  .series-card{
    &__img{
      padding-bottom: 70.6%;
      margin-bottom: .625em;
      &:after{
        display: none;
      }
    }
    &__title,
    &__duration{
      font-size: 1em;
    }
    &__duration{
      margin-top: .25em;
    }
  }
}


@media screen and (max-width: 932px) {
  .player {
    font-size: .8242em;
  }
}


@media screen and (max-width: 576px){
  .player{
    font-size: .6em;
  }
}
