.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg {
        background: rgba(0, 0, 0, 0.35);
        backdrop-filter: blur(5px);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #242426;
        border-radius: 1.25em;
        width: 48.75em;
        max-width: calc(100% - 2.5em);
        overflow-y: auto;
        overflow-x: hidden;
        max-height: 100vh;
        padding: 2em;
        position: relative;
        z-index: 3;
    }
    &__top{
        display: flex;
        justify-content: space-between;
        margin-bottom: 2.5em;
    }
    &__close{
        width: 2em;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $accent;
            }
        }
    }
    &__content{
        @media screen and (min-width: 993px){
            max-height: calc(100 * var(--vh) - 13.75em);
            width: calc(100% + 1em);
            padding-right: 1em;
            overflow-y: auto;
            @include scroll;
        }
    }
}

.source-card{
    display: flex;
    align-items: center;
    min-height: 4.1875em;
    color: #FFFFFF;
    border-radius: .625em;
    padding: .5em 2.1875em;
    cursor: pointer;
    transition: all .3s;
    @include lastMb(2em);
    &:before{
        border: none;
        z-index: -1;
    }
    &__badges{
        display: flex;
        align-items: center;
    }
    &__badge{
        margin-left: .9375em;
        @include center;
        height: 1.6875em;
        border-radius: .3125em;
        padding: 0 .375em;
        color: $black;
        background-color: $yellow;
        font-weight: 600;
        span{
            font-size: 1.5em;
            line-height: 137%;
        }
    }
    &._disable{
        color: $muted;
        pointer-events: unset;
        &:after{
            content: '';
            position: absolute;
            top: 50%;
            right: 1.75em;
            transform: translateY(-50%);
            background-image: url(../img/not-available.svg);
            @include bgImgSize(100%);
            width: 1.625em;
            height: 1.625em;
        }
    }
}

.notification-modal {
    .modal__dialog {
        width: 56.25em;
    }
}

.notification-card{
    padding: 1em 2.1875em;
    position: relative;
    border-radius: .625em;
    @include lastMb(2em);
    &:before{
        content: '';
        position: absolute;
        z-index: -1;
        inset: 0;
        border-radius: inherit;
        background: rgba(125, 127, 146, 0.25);
        backdrop-filter: blur(5px);
    }
    &._new{
        &:before{
            background-color: $accent;
        }
    }
    &__wrap{
        display: flex;
    }
    &__img{
        width: 10.875em;
        flex-shrink: 0;
        margin-right: 1.5625em;
        img{
            border-radius: .3125em;
            width: 100%;
            display: block;
        }
        &~.notification-card__content{
            width: calc(100 - 12.4375em);
        }
    }
    &__content{
        width: 100%;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1em;
    }
    &__date{
        flex-shrink: 0;
        margin-left: .5em;
    }
    &__text{
        p{
            line-height: inherit;
        }
        b{
            font-weight: 700;
        }
    }
}

.auth-modal{
    .modal__dialog{
        width: 47.75em;
    }
    &__btn{
        width: 100%;
        border-radius: .625em;
        height: 5em;
        justify-content: unset;
        color: #fff;
        @include lastMb(1.25em);
        &:before{
            border: none;
        }
        span{
            font-size: 2em;
        }
    }
    &__form{
        .fg{
            @include lastMb(.75em);
        }
        &-btn{
            margin-top: 2.1875em;
            width: 100%;
            height: 5em;
        }
    }
    &__or{
        text-align: center;
        margin-top: 1.75em;
    }
    &__forgot{
        text-align: center;
        margin-top: 1.75em;
        display: flex;
        flex-direction: column;
        align-items: center;
        &-text{
            margin-bottom: .17em;
        }
    }
}

.filter-modal{
    &__btn{
        width: 100%;
        justify-content: unset;
        font-weight: 600;
        color: #fff;
        border-radius: .625em;
        @include lastMb(2em);
        &._active{
            &:before{
                background: $accent;
            }
        }
        &:before{
            border: none;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{

            padding: 1em;
        }
        &__top{
            margin-bottom: 1.25em;
        }
        &__close{
            width: 1.5em;
        }
    }

    .source-card{
        min-height: 3.3125em;
        padding-left: 1.25em;
        padding-right: 1.25em;
        @include lastMb(.625em);
        &__badge{
            padding: 0 .6875em;
            height: 1.4375em;
            span{
                font-size: 1.125em;
            }
        }
    }

    .notification-card{
        padding: 1.5em 1.25em;
        @include lastMb(1.25em);
        &__img{
            width:  6.5em;
            margin-right: .9375em;
            &~.notification-card__content{
                width: calc(100% - 7.4375em);
            }
        }
    }

    .auth-modal{
        &__btn{
            height: 3.3125em;
            padding: 0 1.25em;
            @include lastMb(.625em);
            span{
                font-size: 1.125em;
            }
        }
        &__form{
            .fg{
                margin-bottom: .875em;
            }
            &-btn{
                margin-top: 1.25em;
                height: 3.3125em;
                span{
                    font-size: 1em;
                }
            }
        }
        &__or{
            margin: 1.875em 0 1.625em;
            &-text{
                font-size: .875em;
            }
        }
        &__code{
            margin-top: .2em;
        }
        &__forgot{
            margin-top: 1.875em;
            &-text,
            &-link{
                font-size: .875em;
            }
            &-text{
                margin-bottom: .43em;
            }
        }
    }

    .filter-modal{
        &__btn{
            padding: 0 1.25em;
            height: 3.3125em;
            @include lastMb(.625em);
        }
    }
}
